:root {
    --theme-color: #17aec9;
    --blue-header: #222b37;
    --text-white: #fff;
    --text-light: #dddddd;
    --background-color: #f5f5f5;
    --border-radius: 5px;
}

/* COMMON */
.container {
    max-width: 1530px;
}

body {
    font-family: 'Nunito', sans-serif;
    background-color: var(--background-color);
}

p {
    margin-bottom: 0;
}

ul {
    margin-bottom: 0;
    padding-left: 0;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

.row>* {
    padding-right: 0;
    padding-left: 0;
}

::-webkit-scrollbar {
    width: 5px;
    background-color: #ddd;
}

::-webkit-scrollbar-thumb {
    background-color: var(--theme-color);
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
}

::-webkit-scrollbar:horizontal {
    height: 3px;
    background-color: rgba(206, 198, 198, 0.158);
}

::-webkit-scrollbar-thumb:horizontal {
    background: var(--theme-color);
    border-radius: 10px;

}

/* HEADER */

/* topbar */
.header-topbar {
    background-color: var(--theme-color);
    color: var(--text-white);
    padding: 15px 0;
}

.header-topbar p {
    font-size: 16px;
    padding-left: 15px;
}

.shipping-header,
.call-info,
.emailsubscription {
    display: flex;
    align-items: center;
}

.shipping-header img {
    height: fit-content;
}

.call-info {
    padding-right: 50px;
}

/* main-header */
.main-header {
    background-color: var(--blue-header);
    color: var(--text-white);
}

.navbar-nav .nav-item .nav-link {
    color: var(--text-white);
    padding: 0;
    padding-right: 45px;
}

.navbar-nav .nav-item .nav-link:hover,
.navbar-nav .nav-item .nav-link:focus,
.navbar-nav .nav-item .nav-link.active:hover,
.navbar-nav .nav-item .nav-link.active:focus {
    color: var(--theme-color);
}

.navbar-main {
    text-align: center;
}

.navbar-nav .nav-item .nav-link.active {
    color: var(--text-white);
}

.header-icon {
    padding: 0 15px;
}

.header-icon.header-cart {
    padding-right: 0;
}

.header-icon.header-cart::after {
    background-color: transparent;
}

.navbar-collapse {
    flex-grow: 0;
}

@media (max-width: 1099px) {
    .header-logo img {
        width: 150px;
    }

    .navbar-nav .nav-item .nav-link {
        font-size: 15px;
    }
}

@media (max-width:991px) {
    .header-topbar p {
        font-size: 14px;
    }

    .menu {
        position: absolute;
        background-color: #fff;
        padding: 0;
        margin: 0;
        width: 100%;
        top: 100%;
        z-index: 999;
    }

    .navbar-nav .nav-item {
        padding: 10px 0;
    }

    .navbar-nav .nav-item .nav-link.active,
    .navbar-nav .nav-item .nav-link {
        color: #000;
        font-weight: 600;
    }

    /* sticky-header */
    .main-header.sticky {
        top: 0;
        position: fixed;
        z-index: 50;
        width: 100%;
        left: 0;
        background: #fff;
        -webkit-animation-name: stickySlideDown;
        animation-name: stickySlideDown;
        -webkit-transition: none;
        transition: none;
        animation-direction: normal;
        animation-duration: 0.5s;
        box-shadow: 0px 3px 5px 0px rgba(0, 0, 0, 0.15);
        list-style: none;
    }

    @keyframes stickySlideDown {
        0% {
            opacity: 0.7;
            -webkit-transform: translateY(-50px);
            transform: translateY(-50px);
        }

        100% {
            opacity: 1;
            -webkit-transform: translateY(0);
            transform: translateY(0);
        }
    }
}

@media (max-width: 767px) {
    .call-infomain {
        justify-content: flex-start !important;
        margin-top: 10px;
    }

    .header-topbar {
        padding: 10px 0;
    }

    .header-topbar p {
        padding-left: 5px;
    }

    .header-topbar img {
        width: 22px;
    }

    .call-info img {
        width: 16px;
    }

    .call-info {
        padding-right: 9px;
    }
}

@media (max-width: 882px) {
    .call-info p {
        font-size: 13px;
    }
}

@media (max-width: 575px) {
    .header-logo img {
        width: 127px;
    }

    .header-icon img {
        width: 17px;
    }

    .header-cart img {
        width: 19px;
    }

    .main-header .navbar {
        padding: 15px 0;
    }
}

@media (max-width:399px) {
    .header-logo img {
        width: 112px;
    }
}

/* NEW ADD HEADER CSS */
@media (max-width: 480px) {
    .header-topbar p {
        font-size: 12px;
    }
}

