/* add new */

.shipping-img img {
    padding: 18px;
}

.main-header .container {
    padding: 0;
}

.multibanner-img::before,
.multibanner-img1::before,
.multibanner-img2::before {
    bottom: 0;
    right: 30%;
    position: absolute;
    content: "";
    width: 50%;
    height: 100%;
    opacity: 0;
    z-index: 999;
    background-color: rgba(255, 255, 255, .4);
}

.multibanner-img:hover::before,
.multibanner-img1:hover::before,
.multibanner-img2:hover::before {
    opacity: 1;
    right: 50%;
    transition: all 0.6s ease-in-out
}

.multibanner-img::after,
.multibanner-img1::after,
.multibanner-img2::after {
    bottom: 0;
    left: 30%;
    position: absolute;
    content: "";
    width: 50%;
    height: 100%;
    opacity: 0;
    z-index: 10;
    background-color: rgba(255, 255, 255, .4);
}

.multibanner-img:hover::after,
.multibanner-img1:hover::after,
.multibanner-img2:hover::after {
    opacity: 1;
    left: 50%;
    transition: all 0.6s ease-in-out
}

.multibanner-img,
.multibanner-img1,
.multibanner-img2 {
    position: relative;
    cursor: pointer;
}

.mg-right {
    margin-right: 15px;
}

