    border: none;
    background-color: #000;
    color: #fff;
    text-transform: uppercase;
    position: relative;
    overflow: hidden;
    transition: all 0.4s ease;
}

.product-cart-btn::after {
    content: '';
    width: 100%;
    height: 100%;
    transform: translateY(100%);
    top: 0;
    left: 0;
    z-index: 999;
    position: absolute;
    background-color: var(--theme-color);
}

.product-cart-btn:hover ::after {
