.cart-wrapper {
    background-color: #e5e5e536;
}

.cart-input input {
    height: 35px;
    width: 50px;
    border: 1px solid #dddddd;
    outline: none;
    cursor: pointer;
    text-align: center;
}

.cart-input span {
    font-size: 21px;
    border-left: none;
}

.cart-inner-wrapper p {
    font-weight: 500;
}

.add-product {
    width: 35px;
    height: 35px;
    border: 1px solid #dddddd;
    text-align: center;
    border-left: none;
    cursor: pointer;
}

.cart-add {
    margin-top: 10px;
}

.cart-item h4 {
    font-size: 18px;
    font-weight: 500;
    border-bottom: 1px solid #dddddd;
    padding: 10px 20px;
}

.cart-item {
    background-color: #fff;
    border: 1px solid #dddddd;
    margin-right: 20px;
    border-radius: var(--border-radius);
}

.cart-product-img {
    display: flex;
    justify-content: center;
    margin-right: 15px;
}

.cart-product-img img {
    width: 100px;
    height: 100px;
}

.product-quantity {
    display: flex;
}

.cart-inner-wrapper-main {
    padding: 15px;
}

.cart-inner-wrapper {
    padding: 15px;
}

.cart-pro-price {
    justify-content: center;
    display: flex;
}

.cart-checkout {
    background-color: #fff;
    border: 1px solid #dddddd;
    padding: 20px;
    border-radius: var(--border-radius);
}

.pro-size{
    border: 1px solid #ddd;
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    justify-content: center;
    text-transform: uppercase;
    font-size: 15px;
}

.cart-shipping {
    padding-bottom: 10px;
    font-size: 15px;
    font-weight: 500;
}

.cart-pricemain {
    color: var(--theme-color);
}

.trashicon {
    cursor: pointer;
}

.checkout-btn {
    border: none;
    background-color: #000 !important;
    border: 1px solid #000;
    color: #fff;
    padding: 7px 25px;
    margin-top: 15px;
    border-radius: 4px;
    font-size: 15px;
    z-index: 99;
    transition: all 0.4s ease;
    position: relative;
    overflow: hidden;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 15px;
}

.disbaled {
    border: none;
    background-color: #000 !important;
    border: 1px solid #000;
    color: #fff;
    padding: 7px 25px;
    margin-top: 15px;
    border-radius: 4px;
    font-size: 15px;
    z-index: 99;
    transition: all 0.4s ease;
    position: relative;
    overflow: hidden;
    text-transform: uppercase;
    font-weight: 400;
    margin-bottom: 15px;
    opacity: 0.6;
}

.shopping {
    margin: 80px 0;
}

.cart-padding {
    padding-bottom: 80px;
}

