.checkout-btn:hover {
    color: var(--theme-color);
    background: #fff !important;
    border: 1px solid var(--theme-color);
}

.iphone-cart h5 {
    font-size: 18px;
    font-weight: 500;
    color: #333;
}

.cart-item h4 {
    margin-bottom: 0;
}

/* .cart-input, .iphone-cart-info{
    display: flex;
    justify-content: center;
} */
.cart-discount {
    font-size: 16px;
    font-weight: 700;
    padding: 5px 0;
}

.remove-price {
    font-size: 15px;
    font-weight: 500;
    color: #777;
}

.small-screen-show {
    display: none;
}

@media(max-width: 1599px) {
    .cart-wrapper {
        padding: 0 15px;
    }
}

@media (max-width: 1299px) {
    .iphone-cart-info {
        display: flex;
        justify-content: center;
    }
}

@media (max-width: 1110px){
    .cart-tablemain table{
        width: 700px;
    }
}

@media (max-width: 991px) {
    .trash-main {
        padding-right: 15px !important;
    }

    .iphone-cart .product-quantity {
        justify-content: flex-start !important;
    }

    .cart-pro-price {
        margin-right: -130px;
    }

    .cart-product-img {
        width: 129px;
    }
}

@media (max-width: 899px) {
    .cart-product-img {
        width: 100%;
    }
}

@media (max-width: 852px) {
    .checkout-btn {
        font-size: 14px;
        padding: 7px 15px;
    }
}

@media (max-width: 767px) {
    .cart-item {
        margin-right: 0;
        margin-bottom: 30px;
    }

    .cart-product-img {
        display: flex;
        justify-content: center;
    }

    .cart-pro-price {
        justify-content: center;
    }

    .cart-input {
        margin-top: 10px;
    }

    .trash-main {
        text-align: center !important;
        padding-right: 0 !important;
        padding-top: 20px;
    }

    .cart-padding {
        padding-bottom: 31px;
    }

    .checkout-btn {
        margin-bottom: 30px;
    }

    .cart-checkout .checkout-btn {
        margin-bottom: 10px;
    }

    .cart-main2 .cart-checkout {
        margin-top: 0;
    }
}

